import axios from "../libs/api.request.js";


//公告列表
export const zixun = (data) => {
    return axios.requestVerification({
        url: '/Home/Qbw/zixun',//不验证的
        params: data,
        method: 'get'
    })
};

//公告详情
export const zixun_detail = (data) => {
    return axios.requestVerification({
        url: '/Home/Qbw/zixun_detail',//不验证的
        data,
        method: 'post'
    })
};

