const url = 'www.bitpui.com' // 线上用  axios.baseUrl 
export const mainPushUrl = 'ws://' + url + ":7272"
export const realPushUrl = 'ws://' + url + ":7275"
export const bidaskPushUrl = 'ws://' + url + ":7273"

// 行情列表
export const getPro = (data) => {
    return axios.requestVerification({
        url: '/Home/Trade/get_pro',
        data,
        method: 'post'
    })
};
// 首页行情列表
export const indexGetpro = (data) => {
    return axios.request({
        url: '/home/ajax/getpro',
        params: {
            data
        },
        method: 'get'
    })
};
// 用户余额
export const getBalance = () => {
    return axios.request({
        url: '/Home/Users/get_balance',
        params: {
            type: 0 // usdt id为0
        },
        method: 'get'
    })
};
