<style type="text/css" lang=less>
	@import '../assets/index.less';
	.paginationmain{
		text-align: center;
		padding: 10px;
		.el-pager li:hover{
			color: @main;
		}
		.el-pager li.active{
			color: @main;
		}
		.el-pagination, .el-pager li.btn-quicknext, .el-pager li.btn-quickprev{
			color: #666;
		}
		.el-pagination button:disabled,
		.el-pager li{
			background: transparent;
		}
	}
</style>
<template>
	<div class="paginationmain">
		<section class="pagination">
			<el-pagination  :page-size="size" :current-page="p" :page-count="conut" layout="prev, pager, next" :total="total" @current-change="currentchange" ></el-pagination>
		</section>
	</div>
</template>
<script>
	// page.total 总条数 page和count传一个就行
	// page.count 总页数
	// page.size 每页显示条数 默认10
	// page.p 当前页
	// 事件change为页码改变
	export default {
	    props: {
			page:Object
		},
	    data () {
	        return {
				p:Number(this.page.p) || 1,
				size: Number(this.page.size) || 10
			}
		},
		computed:{
			// 总条数
			total() {
				if (this.page.total) return Number(this.page.total)
				else return this.page.conut * this.size 

			},
			// 总页数
			conut() {
				if (this.page.conut) return Number(this.page.conut)
				else  return ~~(this.page.total / this.size) 
				
			}
		},
	    created () {
	    	let _this = this;
	    	for (var i in _this.page) {
	    		_this.page[i] = Number(_this.page[i]);
	    		// statement
	    	}
	    },
	    watch: {},
	    methods: {
	    	currentchange(row){
	    		this.$emit('change',row);
	    	}
	    }
	}
</script>


